/* eslint-disable */
import PropTypes from 'prop-types';
import React from 'react';

import { Swiper, SwiperSlide } from 'swiper/react';
import SwiperCore, { Pagination, Autoplay } from 'swiper';

export function CourseTeachers({teachers}) {
  return (
    <div className="course__teacher teacher" style={{'max-width': '600px'}}>
      <h2 className="teacher__title">Преподаватели</h2>

      <div className="teacher__list">
        <Swiper
          slidesPerView={5}
          spaceBetween={10}
          // pagination={{
          //   el: '.main-page-swiper__pagination',
          //   clickable: true,
          // }}
          autoplay={{
            delay: 8000,
          }}
        >
          {teachers.map((data) => {
            const {name, trumb, link, id} = data;
            return (
              <SwiperSlide key={id} className="teacher__item">
                <a href={link} className="teacher__img-link">
                  
                  <img src={trumb} alt={name} className="teacher__img" />
                </a>
                <a href="/instructor/3971" className="teacher__link">{name}</a>
              </SwiperSlide>
            );
          })};
        </Swiper>
      </div>
    </div>
  );
}

CourseTeachers.propTypes = {
  teachers: PropTypes.array.isRequired,
};

