/* eslint-disable */
import React from 'react';
import PropTypes from 'prop-types';

export default function PopUpCompare({visible, setVisible, content}) {
  
  // let html = document.getElementsByTagName('html')[0];
  // visible ? html.classList.add("fixed") : html.classList.remove("fixed");

  return (
    <section className={visible ? 'popup-compare' : 'popup-compare popup-compare--hidden'} onClick={() => setVisible(false)}>
      <div className="popup-compare__block" onClick={(e) => e.stopPropagation()}>
        <h2 className="popup-compare__title">Сравните курсы</h2>
        <button className="popup-compare__close" type="button">Добавить курс</button>
        <ul className="popup-compare__list">
          <li className="popup-compare__item popup-compare__item--titles">
            <p className="popup-compare__title-name">Название курса</p>
            <p className="popup-compare__title-name">Прожолжительность</p>
            <p className="popup-compare__title-name">Цена</p>
          </li>
          <li className="popup-compare__item">
            <p className="popup-compare__name">Мва – general management</p>
            <p className="popup-compare__duration">4 месяца</p>
            <p className="popup-compare__price">100.000</p>
            <button className="popup-compare__btn btn">Купить курс</button>
          </li>
          <li className="popup-compare__item">
            <p className="popup-compare__name">Мва – general management</p>
            <p className="popup-compare__duration">4 месяца</p>
            <p className="popup-compare__price">100.000</p>
            <button className="popup-compare__btn btn">Купить курс</button>
          </li>
        </ul>
      </div>
    </section>
  );
}

PopUpCompare.propTypes = {
  visible: PropTypes.bool.isRequired,
  setVisible: PropTypes.func.isRequired,
  content: PropTypes.array.isRequired,
};
